.checkout-page {
  width: 55%;
  min-height: 90vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 50px auto 0;
  @media screen and (max-width: 800px) {
    margin: 20px auto 0;
    width: 70%;
  }
  .checkout-header {
    width: 100%;
    padding: 10px 0;
    display: flex;
    justify-content: space-between;
    border-bottom: 1px solid darkgrey;

    .header-block {
      text-transform: capitalize;
      width: 23%;
      @media screen and (max-width: 800px) {
        font-size: 0.5rem;
      }

      &:last-child {
        width: 8%;
      }
    }
  }

  .total {
    margin-top: 30px;
    margin-left: auto;
    font-size: 36px;
  }
  button {
    margin-left: auto;
    margin-top: 5rem;
  }
  .test-warning {
    text-align: center;
    margin-top: 40px;
    font-size: 1rem;
    color: blue;
  }
}
